<template>
  <div class="user">
    <div class="text-center">
      <div id="wrap" ref="name">
        {{ user.name }}
      </div>
      <p class="user-time">: {{ user.time1 }} {{ user.time2 }}</p>
      <p class="education">: {{ user.education }}</p>
      <p class="motto">: <span ref="motto"> {{ user.motto }}</span></p>
    </div>
  
  </div>
</template>

<script>
  export default {
    name: "user-info",
    data() {
      return {
        user: {
          name: "",
          time1: "",//
          time2: "",//
          education: "",//
          motto: ","
        },
        min: 0, //
        max: 250, //
        color: [250, 0, 0], //rag
        oName: "",//
        oMotto: "",//
        colorL: '',//
        colorR: '',//
        
      }
    },
    created() {
      setInterval(() => {
        this.dateDiff();
      }, 1000)
      
    },
    //
    mounted() {
      //jsref
      this.oName = this.$refs.name;
      this.oMotto = this.$refs.motto;
      setInterval(() => {
        this.changeTextColor();
      })
    },
    methods: {
      /**
       * 
       */
      dateDiff() {
        let date1 = new Date(1993, 1, 28);  //
        let date2 = new Date();    //
        let date3 = date2.getTime() - date1.getTime()  //
        //
        let myYear = new Date(date3).getFullYear() - 1970;
        let myMonth = new Date(date3).getMonth();
        let myDay = new Date(date3).getDate();
        let myMinutes = new Date(date3).getMinutes();
        let mySeconds = new Date(date3).getSeconds();
        //
        let leave1 = date3 % (24 * 3600 * 1000)    //
        let myHours = Math.floor(leave1 / (3600 * 1000))
        this.user.time1 = myYear + "  " + myMonth + "  " + myDay + " ";
        this.user.time2 = myHours + "  " + myMinutes + "  " + mySeconds + " ";
      },
      /**
       * 
       */
      changeTextColor() {
        
        for (let i = 0; i < 3; i++) {
          i %= 3;
          var arrX = (i + 1) % 3;
          var arrY = (i + 2) % 3;
          if (this.color[i] == this.max && this.color[arrX] == this.min) this.color[arrY]++;
          if (this.color[i] == this.min && this.color[arrX] == this.max) this.color[arrY]--;
        }
        //16
        this.colorL = '#' + convert(this.color[0]) + '' + convert(this.color[1]) + '' + convert(this.color[2]) + '';
        this.colorR = '#' + convert(this.color[2]) + '' + convert(this.color[0]) + '' + convert(this.color[1]) + '';
        //IE IE789
        // if (navigator.userAgent.match(/Trident/i) && navigator.match(/MSIE [7|8|9] 0]/i)) {
        //   this.oName.style.filter = "progid:DXImageTransform.Microsoft.gradient(startColostor=" + this.colorL + ",endColorstr=" + this.colorR + ",GradientType=0)";
        // }
        this.oName.style.background = '-webkit-linear-gradient(left,' + this.colorL + ',' + this.colorR + ')';
        this.oName.style.webkitBackgroundClip = 'text';
        this.oName.style.webkitTextFillColor = 'transparent';
        this.oMotto.style.background = '-webkit-linear-gradient(left,' + this.colorL + ',' + this.colorR + ')';
        this.oMotto.style.webkitBackgroundClip = 'text';
        this.oMotto.style.webkitTextFillColor = 'transparent';
        
        //RGB16
        function convert(sRgb) {
          var sRgb = sRgb;
          var sHex = sRgb.toString(16);
          sHex = sHex.length < 2 ? '0' + sHex : sHex;
          return sHex;
        }
      }
    }
  }
</script>

<style scoped>
  @font-face {
    font-family: myFont;
    src: url("../../static/font/luoxi.ttf");
  }
  
  .user {
    margin-top: 150px;
  }
  
  .user-time {
    font-size: 30px;
    margin-top: 50px;
  }
  .education{
    margin-top: 40px;
    font-size: 25px;
  }
  #wrap {
    display: inline-block;
    font-size: 100px;
    font-family: myFont;
  }
  
  .motto {
    margin-top: 40px;
    font-size: 30px;
  }
</style>



